<template>
  <div class="post-wrap">
    <!---- Tab & Navigation ---->

    <!-- Tabs Nav -->
    <ul class="tab-nav-wrap" data-panel="psn-tabs">
        <li class="tab-nav active" data-tabs="section1">TAB 翻譯機 V2</li>
        <li class="tab-nav" data-tabs="section2">TAB 翻譯機 V1</li>
        <li class="tab-nav" data-tabs="section3">TAB 產生器</li>
    </ul>

    <!-- Tabs Content -->
    <div class="tab-content-wrap fade-tab" data-panel="psn-tabs">
        <!-- Tab 1 start -->
        <div class="tab-content active" data-tabs="section1">
            <h3>吉他 TAB 譜翻譯機 V2</h3>

            <ul class="block-wrap">
                <li>吉他 TAB 譜 → 數字簡譜</li>
                <li><a href="https://minruxie.github.io/demo_station/GuitarNotationTool_2">https://minruxie.github.io/demo_station/GuitarNotationTool_2</a></li>
            </ul>
            
            <h4>軌道</h4>
            <ul>
                <li>軌道：每一列可以產生 20 個音符組。</li>
                <li>新增軌道：點擊上方新增按鈕，可以新增一行空的。</li>
                <li>刪除軌道：點擊每列右上角的刪除按鈕，可以刪除該列。</li>
            </ul>
            <h4>音符</h4>
            <ul>
                <li>選擇位置：依照 TAB 譜，點擊想要新增音符的格子（位置），會跳出控制面板。</li>
                <li>新增音符：於控制面板，在下拉式選單中選擇 TAB 數字，並按下勾勾按鈕，即可獲得此格的數字簡譜記號。</li>
                <li>修改音符：於控制面板，在下拉式選單中選擇新的 TAB 數字，並按下勾勾按鈕，即可修改。</li>
                <li>刪除音符：於控制面板，在下拉式選單中選擇「無音符」選項，即可取消該格顯示。</li>
            </ul>


            <h3>部分畫面展示</h3>

            <p>初始畫面</p>
            <div class="img-wrap">
                <img src="@/assets/images/work/guitar_tab_translator_v2/interface_1.png" />
            </div>

            <p>TAB 譜：先在欲選擇的弦上點擊，會出現控制面板，即可選擇格子。</p>
            <div class="img-wrap">
                <img src="@/assets/images/work/guitar_tab_translator_v2/interface_2.png" />
            </div>

            <p>簡譜：選擇完畢後，會出現對應的簡譜音符。</p>
            <div class="img-wrap">
                <img src="@/assets/images/work/guitar_tab_translator_v2/interface_3.png" />
            </div>
        </div>
        <!-- Tab 1 end -->
        
        <!-- Tab 2 start -->
        <div class="tab-content" data-tabs="section2">
            <h3>吉他 TAB 譜翻譯機 V1</h3>

            <ul class="block-wrap">
                <li>吉他 TAB 譜 → 數字簡譜</li>
                <li><a href="https://minruxie.github.io/demo_station/GuitarNotationTool">https://minruxie.github.io/demo_station/GuitarNotationTool</a></li>
            </ul>
            
            <h4>簡譜面板：顯示音符。</h4>
            <ul>
                <li>修改歌名：雙擊歌名區塊可以輸入歌名。</li>
                <li>刪除指定軌道：點擊軌道右上方的叉叉按鈕，即可刪除該軌道及內容。</li>
            </ul>

            <h4>TAB 譜面板：輸入音符。</h4>
            <ul>
                <li>點擊格子即可於「焦點音符後方」新增音符。</li>
            </ul>

            <h4>工具箱面板：右方工具箱（開啟；收合）。</h4>
            <ul>
                <li>刪除：刪除一個「焦點音符」。</li>
                <li>空格：增加空格於「焦點音符後方」。</li>
                <li>新增一行：增加一行軌道。</li>
                <li>關閉/開啟吉他：可控制吉他面板的顯示（開啟；收合）。</li>
            </ul>
        </div>
        <!-- Tab 2 end -->

        <!-- Tab 3 start -->
        <div class="tab-content" data-tabs="section3">
            <h3>吉他 TAB 譜產生器</h3>

            <ul class="block-wrap">
                <li>數字簡譜 → 吉他 TAB 譜</li>
                <li><a href="https://minruxie.github.io/demo_station/GuitarTABTool">https://minruxie.github.io/demo_station/GuitarTABTool</a></li>
            </ul>

            <h4>樂譜面板：顯示音符的地方。</h4>
            <ul>
                <li>修改歌名：雙擊歌名區塊可以輸入歌名。</li>
                <li>刪除指定軌道：點擊軌道右上方的叉叉按鈕，即可刪除該軌道及內容。</li>
            </ul>

            <h4>吉他面板：下方吉他面板。</h4>
            <ul>
                <li>新增音符：按下吉他面板中的弦，即可於畫面上的「焦點音符後方」產生對應的音符。</li>
            </ul>

            <h4>工具箱面板：右方工具箱（開啟；收合）。</h4>
            <ul>
                <li>刪除：刪除一個「焦點音符」。</li>
                <li>空格：增加空格於「焦點音符後方」。</li>
                <li>新增一行：增加一行軌道。</li>
                <li>關閉/開啟吉他：可控制吉他面板的顯示（開啟；收合）。</li>
            </ul>
        </div>
        <!-- Tab 3 end -->
    </div>
  </div>
</template>

<script>
// import jquery
import $ from "jquery";
window.$ = $;

export default {
    name: 'SpGuitarConvert',
    mounted() {
      /* if not mac, hide scroll */
      this.isMac = navigator.platform.toUpperCase().indexOf('MAC') >= 0; // not found = -1
      let $dev_outter_wrap = $('.dev-outter-wrap');
      if ( !this.isMac && $dev_outter_wrap.length > 0 ) {
        $dev_outter_wrap.addClass('hide-scroll');
      }

      /* tabs feature */
      $('.tab-nav').on('click', function(){
        let tab_panel = $(this).parent('.tab-nav-wrap').attr('data-panel');
        
        let tab = $(this).attr('data-tabs');
        $(this).siblings('.active').removeClass('active');
        $(this).addClass('active');
    
        let $tab_content_wrap = $('.tab-content-wrap').filter(`[data-panel="${tab_panel}"]`);
        $tab_content_wrap.find('.active').removeClass('active');
        $tab_content_wrap.find(`[data-tabs="${tab}"]`).addClass('active');

        $('html, body').animate({
            'scrollTop': $(this).offset().top - 70
        }, 300);
      });
    }
}
</script>

